@import "animate.css";

*{
  font-family: Helvetica,Arial, sans-serif;
}
body{
  padding: 60px;
}
input{
  width: 100%;
  font-size: 1.2rem;
  color:gray;
}

.card-grid{
  display: flex;
  flex-flow: row wrap;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 3%;
  width: 30%;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
  min-width: 100%;
  display: block;
  margin: 0 auto;
}